import React, { Component } from 'react';
// import { motion, AnimatePresence } from "framer-motion"
import Tile from '../components/Tile';
import Draggable from "react-draggable";
import colors from '../config/colors';
// const twoIndices = ["0", "1"];
// const threeIndices = ["0", "1", "2"];
const shadow = `0px 0px 38px ${colors.off_black}`;
let idPrefix = "";

class TileSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      xPosition: 0,
      yPosition: 0,
      lastPositionX: 0,
      lastPositionY: 0,
      width: this.props.letters.length * this.props.tileHeight,
      height: this.props.tileHeight,
      letters: this.props.letters,
      flexDirection: "row",
      flipState: this.props.flipState,
      flipping: false,
      beingDragged: false,
      mousePosition: 0,
      bgColor: colors.text_white,
      zIndex: 0,
      show: true,
    };
    this.tileRefs = [];
  }

  componentDidMount(){
    this.setState({xPosition: this.props.tileHeight * 0.4 + this.props.index * 20, yPosition: this.props.index * 10});
  }

  handleDrag(e, data){
    if(!this.state.beingDragged){
      const { clientX, clientY } = e;
      const { node } = data;
  
      const rect = node.getBoundingClientRect();
      const { left, top, width, height } = rect;

      const relativeX = clientX - left;
      const relativeY = clientY - top;
  
      let numDivisions;
      let horizontal;
      let position;

      if ((width / height > 1.8 && width / height < 2.2) || (height / width > 1.8 && height / width < 2.2)) {
        numDivisions = 2;
        horizontal = (width / height > 1.8 && width / height < 2.2)? true:false;
      } else if ((width / height > 2.8 && width / height < 3.2) || (height / width > 2.8 && height / width < 3.2)) {
        numDivisions = 3;
        horizontal = (width / height > 2.8 && width / height < 3.2)? true:false;
      } 

      if(horizontal){
        position = Math.floor((relativeX / width) * numDivisions);
      }else{
        position = Math.floor((relativeY / height) * numDivisions);
      }

      this.setState({beingDragged: true, mousePosition: position, zIndex: 10, shadow: shadow});
    }
  }

  handleStop(e, data){
    console.log("handleStop e.x: " + e.x);

    // const {beingDragged} = this.state
    // this.setState({dragging: false})
    // if (beingDragged) {
    //   this.props.sendStopping(data.x, data.y, this.props.id, this.state.flipState, this.state.mousePosition, e);//this.onDrop(...args)
    // } else {
    //   this.handleClick(e, data);
    // }
    if(!this.state.beingDragged)return;

    setTimeout(() => {
      this.setState({beingDragged: false});
      if(!this.state.flipping)this.setState({zIndex: 0}); 
    }, 200);    
    this.props.sendStopping(data.x, data.y, this.props.id, this.state.flipState, this.state.mousePosition, e);
  }

  handleTouchStop(e){
    const { x, clientX } = e;
    console.log('handleTouchStop x:', x);
    console.log('handleTouchStop clientX:', clientX);
    window.alert("stopping");
    this.props.sendStopping(e.x, e.y, this.props.id, this.state.flipState, this.state.mousePosition, e);
  }

  handleClick(){
    // if(this.state.beingDragged)return;
    window.alert("click 1");

    const newFlipState = (this.state.flipState + 1)%4;
    const tiles = this.props.letters.length === 2?2:3;
    const th = this.props.tileHeight;
    let addToX = 0;
    let addToY = 0;
    let dir = "";
    let letters = this.state.letters;
console.log("click 1");
    if(this.state.flipping){
console.log("click 2");
    window.alert("click 2");
      this.setState({
        width: this.state.width === tiles * this.props.tileHeight?this.props.tileHeight:tiles * this.props.tileHeight,
        height: this.state.height === tiles * this.props.tileHeight?this.props.tileHeight:tiles * this.props.tileHeight,
        flipState: newFlipState,
        letters: newFlipState === 1 || newFlipState === 3? letters:letters.reverse()
      });
      switch(newFlipState){
        case 1:
          addToX = tiles === 2?0:th;
          addToY = tiles === 2?0:-th;
          dir = "column";
          break;
        case 2:
          addToX = -th;
          addToY =  tiles === 2?0:th;
          dir = "row";
          break;
        case 3:
          addToX = th;
          addToY = -th;
          dir = "column";
          break;
        default:
          addToX =  tiles === 2?0:-th;
          addToY = th;
          dir = "row";
      }
      this.setState({flexDirection: dir, xPosition: this.state.xPosition + addToX, yPosition: this.state.yPosition + addToY});
    }else{
    window.alert("click 3");
console.log("click 3");
      const addToDoublesX = this.props.letters.length === 2?3 * th:0;
      const subtractFromDoublesY = this.props.letters.length === 2?-th:0;

      this.setState({
        flipping: true,
        flexDirection: this.state.flexDirection === "row"?"column":"row", 
        xPosition: th/2 + addToDoublesX, 
        yPosition: -(12 * th/2 + this.props.index * th) - subtractFromDoublesY, 
        shadow: shadow,
        width: this.state.width === tiles * th?th:tiles * th,
        height: this.state.height === tiles * th?th:tiles * th,
        flipState: newFlipState,
        letters: newFlipState === 1 || newFlipState === 3? letters:letters.reverse(),
        zIndex: 10
      });
    }
  }

  updatePosition(x, y, overBoard){
    let modX = x;
    let modY = y;;
    const th = this.props.tileHeight;
    window.alert("Inside updatePosition");

    if(overBoard){
      modX = Math.round(x/th) * th;
      modY = Math.round(y/th) * th;
    }

    this.setState({xPosition:  modX, yPosition: modY, shadow: null, flipping: false}); 
  }

  showSolved(refPrefix){
    let index = 0;
    this.state.letters.forEach(element => {
      this.tileRefs[refPrefix + "|" + index].showSolved(refPrefix + "|" + index);
      index++;
    });
  }

  setLastPositionX(x){
    this.setState({lastPositionX: x});
  }
  
  setLastPositionY(y){
    this.setState({lastPositionY: y});
  }

  renderTile(letter, i){
    const refPrefix = idPrefix + this.props.index + "|";

    return (
      <Tile
        key={`${refPrefix}${i}`}
        id={`${refPrefix}${i}`}
        letter={letter}
        ref={(ref) => this.tileRefs[`${refPrefix}${i}`] = ref}
        myRef={`${refPrefix}${i}`}
        tileHeight={this.props.tileHeight}
        // z={this.state.zIndex}
        delay={this.props.delay}
      />
    )
  }

  render() {
    const { letters, left, top } = this.props;
    idPrefix = letters.length === 2? "d|":"t|";

    return (
      <Draggable 
        position={{x: this.state.xPosition, y: this.state.yPosition}}
        onStart={(e, data) => {
          this.setLastPositionX(Math.floor(data.x));
          this.setLastPositionY(Math.floor(data.y));
        }}
        onDrag={(e, data) => this.handleDrag(e, data)}
        onStop={(e, data) => {
          // e.stopPropagation();
          // console.log("data.x: " + data.x);
          // console.log("this.state.lastPositionX: " + this.state.lastPositionX);
          // console.log("data.y: " + data.y);
          // console.log("this.state.lastPositionY: " + this.state.lastPositionY);
          if (Math.floor(data.x) === this.state.lastPositionX && Math.floor(data.y) === this.state.lastPositionY) {
            // drag did not change anything. Consider this to be a click
            console.log("click");
            // this.handleClick();
            return;
          }else{
            window.alert("in here!");
            window.alert(e.x);
            console.log(e.x);
            
            this.handleStop(e, data);

          }
        }}
        // onStop={(e, data) => this.handleStop(e, data)}
      > 
        <div 
        onTouchEnd={(e) => this.handleTouchStop(e)}
          // onClick={() => this.handleClick()} 
          // onTouchEnd={() => this.handleClick()}
          // onTouchStart={(evt) => {
          //   evt.stopPropagation();
          //   this.handleClick();
          // }}
          style=
            {{
              ...styles.tileset, 
              flexDirection: this.state.flexDirection, 
              height: this.state.height, 
              width: this.state.width, 
              top: top, 
              left: left,
              zIndex: this.state.zIndex,
              boxShadow: this.state.shadow
            }}>
            {
              letters.map((letter, index) => this.renderTile(letter, index))
            }
        </div>
      </Draggable>
    );
  }
}

const styles = {
  tileset: {
    position: 'absolute',
    display: 'flex',
    marginTop: 2,
    marginLeft: 2,
    justifyContent: "space-around",
    backgroundColor: colors.translucent,
  },
}

export default TileSet;